/* STATIC STYLES */

.hero {
  position: relative;
  min-height: 100vh;
  padding: 0 1rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
}

.hero-title > span:first-child {
  font-weight: lighter;
}

.fade-in {
  animation: fade-in 0.7s ease-out forwards;
  opacity: 0;
}

.fade-in.delay-1 { animation-delay: 0.2s; }
.fade-in.delay-2 { animation-delay: 0.4s; }
.fade-in.delay-3 { animation-delay: 0.5s; }

@keyframes fade-in {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: bounce 2s infinite;
  color: var(--color-muted-foreground);
  font-size: 0.875rem;
}

.arrow-icon {
  width: 1.25rem;
  height: 1.25rem;
  color: var(--color-primary);
}

@keyframes bounce {
  0%, 100% { transform: translateX(-50%) translateY(0); }
  50% { transform: translateX(-50%) translateY(-10px); }
}

/* RESPONSIVE STYLES */

.hero-title {
  font-size: 2.75rem;
  font-weight: bold;
}

.hero-subtitle {
  font-size: 1.125rem;
  color: var(--color-primary-text-dark);
}

@media (max-width: 1200px) {
  .hero-title {
    font-size: 2.75rem;
  }

  .hero-subtitle {
    font-size: 1.3rem;
  }
}

@media (max-width: 992px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1.2rem;
  }
}

@media (max-width: 768px) {
  .hero-title {
    font-size: 2.25rem;
  }

  .hero-subtitle {
    font-size: 1rem;
    padding: 0 1rem;
  }
}

@media (max-width: 576px) {
  .hero-title {
    font-size: 1.75rem;
  }

  .hero-subtitle {
    font-size: 0.95rem;
  }
}