@import './root-styles.css';
@import './root-utils.css';
@import './utils.css';

/* STATIC STYLES */

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

main {
  flex: 1;
}

section {
  max-width: 1200px;
  place-content: center;
  text-align: center;
  margin: auto;
}

/* RESPONSIVE STYLES */

section:not(:first-child) {
  margin: 0 auto;
  padding: 12rem 3rem;
}

@media (max-width: 1200px) {
  section:not(:first-child) {
    padding: 10rem 3rem;
  }
}

@media (max-width: 992px) {
  section:not(:first-child) {
    padding: 7rem 2rem;
  }
}

@media (max-width: 768px) {
  section:not(:first-child) {
    padding: 5rem 1rem;
  }
}

@media (max-width: 576px) {
  section:not(:first-child) {
    padding: 3rem 1rem;
  }
}