/* STATIC STYLES */

.carousel-image-container {
  display: flex;
  gap: 5rem;
}

.carousel-container {
  margin-top: 1rem;
  margin-bottom: -2rem;
}

.carousel.carousel-slider {
  padding: 0 0 3rem 0;
}

.carousel.carousel-slider .control-arrow {
  display: none;
}

.carousel .control-dots .dot {
  width: 0.6rem !important;
  height: 0.6rem !important;
}

/* RESPONSIVE STYLES */

.carousel-image {
  height: 20rem;
  object-fit: contain;
}

@media (max-width: 1200px) {
  .carousel-image {
    height: 19rem;
  }
}

@media (max-width: 992px) {
  .carousel-image {
    height: 14rem;
  }
}

@media (max-width: 768px) {
  .carousel-image {
    height: 7rem;
  }
}

@media (max-width: 576px) {
  .carousel-container {
    margin: 0;
  }

  .carousel-image {
    height: 5rem;
  }
}