/* STATIC STYLES */

.star-background {
  position: fixed;
  inset: 0;
  overflow: hidden;
  pointer-events: none;
  z-index: 0;
}

.star {
  position: absolute;
  border-radius: 50%;
  background: var(--color-primary-text);
  box-shadow: 0 0 10px 2px var(--color-primary-text-dark);
  animation: pulse-subtle 4s ease-in-out infinite;
}

.meteor {
  position: absolute;
  border-radius: 5rem;
  background: linear-gradient(to right, var(--color-primary-text), var(--color-primary-text-dark), transparent);
  box-shadow: -20px 0 50px 5px var(--color-primary-text-dark);
  animation: meteor 5s linear infinite;;
}

@keyframes pulse-subtle {
  0%,
  100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes meteor {
  0% { transform: rotate(215deg) translateX(0); opacity: 1; }
  70% { opacity: 1; }
  100% { transform: rotate(215deg) translateX(-500px); opacity: 0; }
}

