/* STATIC STYLES */

.portfolio {
  display: flex;
  flex-direction: column;
  gap: 4rem;
  padding-inline: 3rem;
  width: 100vw;
}

.portfolio-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
  position: relative;
  margin: auto;
}

/* RESPONSIVE STYLES */

.portfolio-cards > .card3:not(:first-child) {
  margin-top: -10rem;
}

.portfolio-cards > .card3:nth-child(odd) {
  margin-right: calc(50% + 4rem);
  text-align: center;
}

.portfolio-cards > .card3:nth-child(even) {
  margin-left: calc(50% + 4rem);
  text-align: center;
}

@media (max-width: 1200px) {
  .portfolio-cards > .card3:not(:first-child) {
    margin-top: 1rem;
  }

  .portfolio-cards > .card3:nth-child(odd) {
    margin-right: 0;
  }

  .portfolio-cards > .card3:nth-child(odd),
  .portfolio-cards > .card3:nth-child(even) {
    margin-left: 0;
  }
}

@media (max-width: 992px) {

}

@media (max-width: 768px) {
  .portfolio-cards > .card3:nth-child(odd),
  .portfolio-cards > .card3:nth-child(even) {
    margin-left: 0;
  }
}

