/* STATIC STYLES */

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  padding: 1.25rem 2rem;
  margin: 0 auto;
  z-index: 40;
  background-color: transparent;
  transition: all 0.3s ease;
}

.header-scrolled {
  padding: 1rem 1.75rem;
  background-color: var(--color-blur);
  backdrop-filter: blur(8px);
}

.header-logo {
  font-size: 1.25rem;
  font-weight: bold;
  color: var(--color-secondary);
}

.nav-link {
  color: var(--color-primary-text-dark);
  text-decoration: none;
  margin-left: 2rem;
  transition: color 0.3s;
}

.nav-link:hover {
  color: var(--color-secondary);
}

.mobile-nav.open {
  opacity: 1;
  pointer-events: auto;
}

/* RESPONSIVE STYLES */

.desktop-nav {
  display: flex;
}

.mobile-nav {
  display: none;
}

.menu-button {
  display: none;
}

