}

@media (max-width: 992px) {

}

@media (max-width: 768px) {
  .desktop-nav {
    display: none;
  }

  .mobile-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    position: fixed;
    inset: 0;
    height: 100vh;
    z-index: 40;
    font-size: 1.25rem;
    background-color: var(--color-card);
    backdrop-filter: blur(8px);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;
  }

  .menu-button {
    display: block;
    font-size: 1.6rem;
    color: var(--color-primary-text);
    background: none;
    border: none;
    cursor: pointer;
    z-index: 50;
  }
}

