/* STATIC STYLES */

/* RESPONSIVE STYLES */

.experience {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}

.experience-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.experience-cards > * {
  flex: 0 0 20rem;
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {

}

@media (max-width: 768px) {
  .experience {
    padding-inline: 8rem;
  }

  .experience-cards {
    flex-direction: column;
  }

  .experience-cards > * {
    flex: 1;
  }
}

@media (max-width: 576px) {
  .experience {
    padding-inline: 2rem;
  }
}